<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .img-w>img {
    width: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .erp-p-no {
    color: #666;
    font-size: 12px;
  }
  
  .stock-num .el-button--mini{
    padding: 6px 5px;
  }
  .sync-file {
    color: red;
    cursor: pointer;
  }
</style>
<template>
  <div>
    <el-table
      class="fixed-height"
      :data="tableData"
      empty-text="没有查询到您想要的数据"
      style="width:100%;"
      size="small">
      <el-table-column min-width="120" prop="triggerType" label="活动类型" :formatter="triggerTypeFormater"></el-table-column>
      <el-table-column min-width="80" prop="id" label="活动编码"></el-table-column>
      <el-table-column min-width="140" prop="title" label="活动名称"></el-table-column>
      <el-table-column min-width="210" label="活动时间">
        <template slot-scope="scope" size="small">
          <div>
            <p>{{scope.row.beginTime.slice(0, 10) + ' 至 ' + scope.row.endTime.slice(0, 10)}}</p>
          </div>
        </template>
      </el-table-column>
      <el-table-column min-width="86" prop="cityName" label="参与区域"></el-table-column>
      <el-table-column min-width="210" label="优惠券有效期">
        <template slot-scope="scope" size="small">
          <div v-if="scope.row.useDayCount">
            {{scope.row.useDayCount}} 天
          </div>
          <div v-else-if="scope.row.useBeginTime && scope.row.useEndTime">
            {{scope.row.useBeginTime.slice(0, 10) + ' 至 ' + scope.row.useEndTime.slice(0, 10)}}
          </div>
        </template>
      </el-table-column>
      <el-table-column min-width="110" prop="totalCount" label="优惠券总张数"></el-table-column>
      <el-table-column min-width="90" prop="status" label="活动状态" :formatter="statusFormater"></el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        min-width="180">
        <template slot-scope="scope" size="small">
          <div>
            <div>
              <el-button type="text" size="mini" icon="el-icon-view" @click="showItem(scope.row)">查看</el-button>
              <el-button type="text" size="mini" icon="el-icon-view" @click="checkItem(scope.row)">查看优惠券</el-button>
              <el-button v-if="scope.row.status === 0" type="text" size="mini" icon="el-icon-setting" @click="turnItem(scope.row, true)">开始</el-button>
              <el-button v-if="scope.row.status === 0 || scope.row.status === 1 || scope.row.status === 5" type="text" size="mini" icon="el-icon-circle-close" @click="turnItem(scope.row)">关闭</el-button>
              <!-- 未开始和一开始的积分换券可以修改库存 -->
              <el-button type="text" size="mini" icon="el-icon-edit" @click="changeStock(scope.row)" v-if="scope.row.triggerType === 121 && (scope.row.status === 1 || scope.row.status === 0)">修改库存</el-button>
            </div>
            <div v-if="scope.row.triggerType === 81">
              <el-button type="text" size="mini" icon="el-icon-edit-outline" @click="showScanCode(scope.row)">生成二维码</el-button>
            </div>
            <div v-if="scope.row.triggerType === 12 && scope.row.isOperate == 1 && (scope.row.status === 1 || scope.row.status === 5)">
              <el-button type="text" size="mini" icon="el-icon-edit-outline" @click="operation(scope.row.id)">发给客户</el-button>
            </div>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import * as utils from '@/utils/utils'
export default {
  data() {
    return {
      tableData: []
    }
  },
  methods: {
    /**
     * [setDefaultMesg 设置信息]
     * @param {[type]} _val [description]
     */
    setDefaultMesg(_val) {
      this.tableData = _val.tableData
    },
    /**
     * [triggerTypeFormater 活动格式化]
     * @param  {[type]} row       [description]
     * @param  {[type]} column    [description]
     * @param  {[type]} cellValue [description]
     * @return {[type]}           [description]
     */
    triggerTypeFormater(row, column, cellValue) {
      // 活动类型，触发条件 1:活动-发放;2:分享;3:签到;4:充值（单次发放）;6:领取；71.消费；42.充值(多次发放),12：活动-指定用户发券  81 扫码送券  82 扫码消费送券
      switch(cellValue) {
        case 1:
          return '直接发放'
        break;
        case 4:
          return '充值返券(单次)'
        break;
        case 42:
          return '充值返券(多次)'
        break;
        case 71:
          return '消费发放'
        break;
        case 2:
          return '分享返券'
        break;
        case 3:
          return '签到返券'
        break;
        case 6:
          return '领取活动'
        break;
        case 81:
          return '扫码送券'
        break;
        case 82:
          return '扫码消费送券'
        break;
        case 91:
          return '省钱礼包'
        break;
        case 12:
          return '指定客户'
        break;
        case 111:
          return '天降红包'
        break;
        case 121:
          return '积分换券'
        break;
        default:
          return '其它券'
        break;
      }
    },
    statusFormater(row, column, cellValue) {
      switch(cellValue) {
        case 0:
          // row.operate = '开始'
          return '未开始'
        break;
        case 1:
          // row.operate = '关闭'
          return '已开始'
        break;
        case 2:
          // row.operate = ''
          return '已结束'
        break;
        case 3:
          // row.operate = ''
          return '已关闭'
        break;
        case 5:
          // row.operate = '关闭'
          return '已分发'
        break;
      }
    },
    showItem(_row) {
      this.$emit("showItem", _row)
    },
    checkItem(_row) {
      this.$emit("checkItem", _row)
    },
    turnItem(_row, on) {
      let val = {
        row: _row,
        on: on
      }
      this.$emit("turnItem", val)
    },
    showScanCode(_row) {
      this.$emit("showScanCode", _row)
    },
    operation(id) {
      this.$emit("operation", id)
    },
    /**
     * @description: 积分换券修改库存
     * @param {type} 
     * @return: 
     */
    changeStock(row) {
      this.$emit("changeStock", row)
    }
  }
}
</script>



