<style scoped>
  .stock-input {
    width: 120px !important;
  }
  ul.icon-add-wrap-i {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap !important;
    margin-bottom: -10px;
  }
  li.group-buy-add-wraper {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    border: 1px solid #ececec;
    padding: 5px 8px;
    margin-right: 10px;
    margin-bottom: 10px;
  }
  .act-img-w-h-dis {
    width: 42px;
    height: 42px;
  }
  li .act-tip-txt {
    padding: 13px 8px 0;
    font-size: 14px;
  }
  .group-buy-add-wraper:hover {
    border: 1px solid #3080fe;
  }
  .chooseUsers .el-dialog__body {
    padding-top: 5px;
  }
  .chooseUsers .chooseUsers-title{
    font-size: 16px;
    font-weight: bold;
  }
  .upload{
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid #dcdfe6;
    color: #fff;
    -webkit-appearance: none;
    text-align: center;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    padding: 7px 15px;
    font-weight: 500;
    border-radius: 3px;
    background: #2077FF;
  }
  .list-wrap {
    min-height: 150px;
    max-height: 400px;
    overflow-y: auto;
  }
  .list-wrap .list-item {
    padding: 10px 0;
    border-bottom: 1px solid #EBEEF5
  }
</style>

<template>
  <div>
    <div class="handel-search-wraper">
      <ul class="handel-wraper icon-add-wrap-i">
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/fafang.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">直接发放</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_direct')" size="mini">立即创建</el-button>
          </div>
        </li>
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/xiao.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">消费发放</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_consumption')" size="mini">立即创建</el-button>
          </div>
        </li>
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/chong.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">充值返券</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_charge')" size="mini">立即创建</el-button>
          </div>
        </li>
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/saoma.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">扫码送券</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_scan')" size="mini">立即创建</el-button>
          </div>
        </li>
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/sheng.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">省钱礼包</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_save')" size="mini">立即创建</el-button>
          </div>
        </li>
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/customer.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">指定客户</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_customer')" size="mini">立即创建</el-button>
          </div>
        </li>
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/package.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">天降红包</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_red')" size="mini">立即创建</el-button>
          </div>
        </li>
        <li class="group-buy-add-wraper">
          <div>
            <img src="/shop/static/imgs/point.png" alt="" class="act-img-w-h-dis">
          </div>
          <div class="act-tip-txt">积分换券</div>
          <div>
            <el-button type="primary" class="sup-mini" @click="addCoupon('add_point')" size="mini">立即创建</el-button>
          </div>
        </li>
      </ul>

      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="活动名称">
            <el-input class="add-input-inline" v-model.trim="searchContent.title" placeholder="请输入活动名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="开始日期">
            <el-date-picker
              class="add-input-inline"  
              v-model="searchContent.promStartDate"
              type="date"
              placeholder="选择开始日期"
              size="small"
              @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="结束日期">
            <el-date-picker
              class="add-input-inline"  
              v-model="searchContent.promEndDate"
              type="date"
              placeholder="选择结束日期"
              size="small"
              @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="门店">
            <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择所属门店" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in storeList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="类型">
            <el-select class="add-input-inline" v-model="searchContent.triggerType" filterable placeholder="请选择类型" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in triggerTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>          
        </el-form>
      </div>
    </div>
    <!-- 搜索结果 活动状态 0:未开始;1:已开始;2:已结束;3:已关闭;5:已分发"-->
    <div class="table-wraper tab-wraper" v-loading = "loading">
      <el-tabs v-model="activeName"  @tab-click="handleTabClick">
        <el-tab-pane :name="item.activeName" v-for="(item, idx) in promStatusOptions" :key="idx">
          <span slot="label">{{item.label}} {{item.activeCount}}</span>
        </el-tab-pane>
        <my-table ref="myTable" @showItem="showItem" @checkItem="checkItem" @turnItem="turnItem" @showScanCode="showScanCode" @operation="operation" @changeStock="changeStock"></my-table>
      </el-tabs>

      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNo"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      title="查看二维码" 
      :visible.sync="dialogFormVisible" 
      width='420px'>
      <div style="text-align: center;">
        <img :src="sacnCodeImgUrl" alt="" style="width: 300px;height: 300px;">
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="downloadIamge(sacnCodeImgUrl, 'code.jpg')" size="mini">下载二维码</el-button>
        <div class="shareline" style="display: inline-block;" v-clipboard:copy="sacnCodeImgUrl" v-clipboard:success="onCopySuc" v-clipboard:error="onCopyError">
           <el-button type="primary" size="mini">复制链接</el-button>
        </div>
        <el-button @click="dialogFormVisible = false" size="mini">关 闭</el-button>
      </div>
    </el-dialog>

    <!-- 发给客户 -->
    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      title="指定客户发券" 
      :visible.sync="chooseUsersShow" 
      class="chooseUsers" 
      width="500px">
      <div>
        <label for="excelFile" class="excelFileLabel">
          <span class="upload">批量导入客户</span>
          <input type="file" id="excelFile" v-if="file.ishowFile" hidden @change="handleFileSelect"/>
          <p style="display: inline-block;">{{ file.name }}</p>
        </label>
        <el-button type="primary" @click="phoneModel" size="mini">下载模板</el-button>
      </div>
      <div style='margin-top: 10px'>
        <el-table
          class="fixed-height"
          size="small"
          :data="phoneListData"
          style="width:100%;"
          max-height="250"
          border>
          <el-table-column width="160" type="index" label="序号" align="center"></el-table-column>
          <el-table-column prop="phoneNo" label="手机号" align="center"></el-table-column>
        </el-table>
      </div>
      <div style='margin-top: 10px'>
        <span class="chooseUsers-title" style='margin-bottom: 5px'>备注：</span>
        <el-input
          type="textarea"
          :rows="2"
          placeholder="请输入内容"
          v-model="remark">
        </el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="sentUser(true)" size="mini">确定发放</el-button>
        <el-button @click="sentUser(false)" size="mini">取  消</el-button>
      </div>
    </el-dialog>

    <!-- 修改库存 -->
    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      title="修改库存" 
      :visible.sync="changeStockDialogShow" 
      width='620px'>
      <div v-loading="changeStockDialogLoading" style="min-height: 150px;">
        <div class="list-wrap" v-if="currentPromDiscount.promDiscounts && currentPromDiscount.promDiscounts.length && promType === '2'">
          <div class="list-item" v-for="(item, index) in currentPromDiscount.promDiscounts" :key="index">
            ({{index + 1}})、
            满 {{item.meetAmount}} 元减 {{item.discAmount}} 元，
            共 {{item.sendPromCount || item.count}} 张，
            共 {{item.discAmount * item.sendPromCount || item.discAmount * item.count || 0}} 元，
            积分购买要 {{item.triggerIntegralProm}} 积分，
            库存：<el-input size="small" class="stock-input" v-model="item.curStock">
              <template slot="append">张</template>
            </el-input>，每人限购数量(选填) {{item.limitCount || '不限'}} 次
          </div>
        </div>

        <div class="list-wrap" v-else-if="currentPromDiscount.promDiscounts && currentPromDiscount.promDiscounts.length && promType === '5'">
          <div class="list-item" v-for="(item, index) in currentPromDiscount.promDiscounts" :key="index">
            ({{index + 1}})、
            使用范围：{{item.goodsType === 1 ? '门店' : '商城'}}，
            应付价格：{{item.needPayAmount || 0}}元 ，
            选择商品：[<span v-for="(i, idx) in item.goodsName" :key="idx">{{i}}，</span>]，
            商品数量小于等于：{{item.productQtyLimit}}({{item.productUnitStr || '份'}})，
            优惠券数量：{{item.sendPromCount || 0}}，
            积分购买需要：{{item.triggerIntegralProm || '-'}}分，
            库存：<el-input size="small" class="stock-input" v-model="item.curStock">
              <template slot="append">张</template>
            </el-input>，
            每人限购数量(选填)：{{item.limitCount || '不限'}} 次
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="doChangeStockHandel" size="mini" :disabled="isDisabled">确认</el-button>
        <el-button @click="changeStockDialogShow = false" size="mini">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import MyTable from "./MyTable"
  import exportSheet from '@/utils/exportSheet'
  const reqUrl = {
    // 获取门店列表接口
    userStoreList: '/foundation/store/userStoreList',
    // 批量优惠券列表
    batchPromList: '/member/erp/prom/batchPromList',
    // 批量优惠券状态更改
    updateBatchPromStatus: '/member/erp/prom/updateBatchPromStatus',
    // 指定发券-用户发券
    giveSpecifyProm: '/member/erp/prom/giveSpecifyProm',
    // 优惠券详情
    batchCreatePromInfo: "/member/erp/prom/batchCreatePromInfo",
    updatePromStock: "/member/erp/prom/updatePromStock"
  }
  let countReg = /^\+?[1-9][0-9]*$/;
  export default {
    data() {
      return {
        loading: false,
        pageNo: 1,
        pageSize: 10,
        total: 0,
        // 门店列表下拉框
        storeList: [],
        searchContent: {
          title: '', // 活动名称
          promStartDate: '', // 开始日期
          promEndDate: '', // 结束日期
          storeNo: '',
          triggerType: '',
          promStatus: '',
          isSpecify: ''
        },
        // 活动类型，触发条件 1:活动-发放;2:分享;3:签到;4:充值（单次发放）;6:领取；71.消费；42.充值(多次发放),12：活动-指定用户发券
        triggerTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '直接发放',
            value: 1
          },
          {
            label: '消费发放',
            value: 71
          },
          {
            label: '充值返券(单次)',
            value: 4
          },
          {
            label: '充值返券(多次)',
            value: 42
          },
          {
            label: '扫码送券',
            value: 81
          },
          {
            label: '扫码消费送券',
            value: 82
          },
          {
            label: '省钱礼包',
            value: 91
          },
          {
            label: '指定客户',
            value: 12
          },
          {
            label: '天降红包',
            value: 111
          },
          {
            label: '积分换券',
            value: 121
          }
        ],
        // 0:未开始;1:已开始;2:已结束;3:已关闭;5:已分发
        promStatusOptions: [
          {
            label: '全部',
            activeName: 'all',
            activeCount: 0,
            value: ''
          },
          {
            label: '未开始',
            activeName: 'activeName0',
            activeCount: 0,
            value: 0
          },
          {
            label: '已开始',
            activeName: 'activeName1',
            activeCount: 0,
            value: 1
          },
          {
            label: '已分发',
            activeName: 'activeName5',
            activeCount: 0,
            value: 5
          },
          {
            label: '已结束',
            activeName: 'activeName2',
            activeCount: 0,
            value: 2
          },
          {
            label: '已关闭',
            activeName: 'activeName3',
            activeCount: 0,
            value: 3
          }
        ],
        tableData: [],
        // 点击切换的tab的名称
        activeName: 'all',
        dialogFormVisible: false, // 导入数据
        phoneListData: [],
        promRecordId: '',
        chooseUsersShow: false,
        sacnCodeImgUrl: '',
        tempConfig: {
          phoneNo: '客户手机号'
        },
        file: {
          name: '',
          ishowFile: true
        },
        remark: '',

        changeStockDialogShow: false,
        changeStockDialogLoading: false,

        // 修改库存使用字段内容
        isDisabled: false,
        detailParamMesg: {},
        disCoupon: {}, // 满减券
        goodCoupon: {}, // 商品券
        disIsnull: true, // 满减券是否是空
        goodIsnull: true, // 商品券是否是空
        promType: '2',
        currentPromDiscount: {} // 当前展示的卡券类型
      }
    },
    created() {
      this.getStoreList()
    },
    mounted() {
      let searchContentMesg = Storage.get("searchContentForCoupon")
      if(this.$route.meta.keepAlive && searchContentMesg) {
        this.pageNo = searchContentMesg.pageNo
        this.searchContent = searchContentMesg.searchContent
        this.activeName = searchContentMesg.activeName
      }
      Storage.remove("searchContentForCoupon")
      this.doSearchHandel()
    },
    components: {
      "my-table": MyTable
    },
    methods: {
      // add_red: 天降红包  add_customer: 指定客户  add_save: 省钱礼包 add_scan: 扫码送券 add_charge: 充值返券 add_consumption: 消费发放 add_direct: 直接发放 add_point: 积分换券
      addCoupon(type) {
        switch(type) {
          case 'add_direct':
            this.$router.push("/discount_coupon/add_direct")
            break;
          case 'add_consumption':
            this.$router.push("/discount_coupon/add_consumption")
            break;
          case 'add_charge':
            this.$router.push("/discount_coupon/add_charge")
            break;
          case 'add_scan':
            this.$router.push("/discount_coupon/add_scan")
            break;
          case 'add_save':
            this.$router.push("/discount_coupon/add_save")
            break;
          case 'add_customer':
            this.$router.push("/discount_coupon/add_customer")
            break;
          case 'add_red':
            this.$router.push("/discount_coupon/add_red")
            break;
          case 'add_point':
            this.$router.push("/discount_coupon/add_point")
            break;
          default:
            this.$notify.warning({
              title: '暂无此类型优惠券',
              message: e.message
            })
            break;
        }

      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(reqUrl.userStoreList, {pageSize: -1}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              let optionsArr = []
              for(let item of res.data.result) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }

              this.storeList = optionsArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [handleTabClick tab点击切换] 
       * @return {[type]} [活动状态 0:未开始;1:已开始;2:已结束;3:已关闭;5:已分发"]
       */
      handleTabClick(_val) {
        if(_val.name == 'all') {
          this.searchContent.promStatus = ''
        }else if(_val.name == 'activeName0') {
          this.searchContent.promStatus = 0
        }else if(_val.name == 'activeName1') {
          this.searchContent.promStatus = 1
        }else if(_val.name == 'activeName2') {
          this.searchContent.promStatus = 2
        }else if(_val.name == 'activeName3') {
          this.searchContent.promStatus = 3
        }else if(_val.name == 'activeName5') {
          this.searchContent.promStatus = 5
        }
        this.activeName = _val.name
        this.doSearchHandel(true)
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNo = value
        this.doSearchHandel()
      },

      /**
       * [doSearchHandel 搜索]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        this.loading = true
        if(_param === true) {
          this.pageNo = 1
        }
        this.$http.post(reqUrl.batchPromList, {...this.searchContent, pageSize: this.pageSize, pageNo: this.pageNo}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.pageVo && res.data.pageVo.result && res.data.pageVo.result.length) {
              this.tableData = res.data.pageVo.result
              let _value = {
                tableData: this.tableData
              }
              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
              this.total = res.data.pageVo.total
            }else {
              this.tableData = []
              let _value = {
                tableData: this.tableData
              }
              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }
              for(let item of this.promStatusOptions) {
                item.activeCount = 0
              }
              this.total = 0
            }
            // 0:未开始;1:已开始;2:已结束;3:已关闭;5:已分发
            for(let item of this.promStatusOptions) {
              if(item.value === '') {
                item.activeCount = res.data.allCount || 0
              }else if(item.value === 0) {
                item.activeCount = res.data.startedCount || 0
              }else if(item.value === 1) {
                item.activeCount = res.data.goingCount || 0
              }else if(item.value === 2) {
                item.activeCount = res.data.endCount || 0
              }else if(item.value === 3) {
                item.activeCount = res.data.closeCount || 0
              }else if(item.value === 5) {
                item.activeCount = res.data.sendCount || 0
              }
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [showItem 查看]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showItem(_row) {
        // 存储查询条件
        let _paramMesg = {
          pageNo: this.pageNo,
          activeName: this.activeName,
          searchContent: this.searchContent
        }
        Storage.set("searchContentForCoupon", _paramMesg)
        let batchCreatePromId = _row.id
        this.$router.push(`/discount_coupon/showcp/${batchCreatePromId}`)
      },
      /**
       * [checkItem 查看优惠券]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      checkItem(_row) {
        let id = _row.id
        this.$router.push(`/dis_search/activity_search/${id}`)
      },
      /**
       * [turnItem 终止]
       * @param  {[type]} val [description]
       * @return {[type]}      [description]
       */
      turnItem(val) {
        this.loading = true
        let batchCreatePromId = val.row.id
        let status_ = ''
        if(val.on == true) {
          status_ = 1
        }else{
          status_ = 3
        }
        this.$http.post(reqUrl.updateBatchPromStatus, {batchCreatePromId: batchCreatePromId, status: status_ }).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.$message.success("设置成功");
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [showScanCode 显示二维码]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showScanCode(_row) {
        let sacnCodeImgUrl = _row.codeUrl
        this.dialogFormVisible = true
        this.sacnCodeImgUrl = sacnCodeImgUrl
      },
      downloadIamge (imgsrc, name) { // 下载图片地址和图片名
        let image = new Image();
        // 解决跨域 Canvas 污染问题
        image.setAttribute("crossOrigin", "anonymous")
        image.onload = function() {
          let canvas = document.createElement("canvas")
          canvas.width = image.width
          canvas.height = image.height
          let context = canvas.getContext("2d")
          context.drawImage(image, 0, 0, image.width, image.height)
          let url = canvas.toDataURL("image/png") //得到图片的base64编码数据
          let a = document.createElement("a") // 生成一个a元素
          let event = new MouseEvent("click") // 创建一个单击事件
          a.download = Date.parse(new Date()) + name || "photo" // 设置图片名称
          a.href = url // 将生成的URL设置为a.href属性
          a.dispatchEvent(event) // 触发a的单击事件
        }
        image.src = imgsrc
      },
      /**
       * [copyUrl 复制链接成功]
       * @return {[type]}      [description]
       */
      onCopySuc() {
        this.$message.success("链接已复制至剪切板")
      },
      /**
       * [onCopyError 复制链接失败]
       * @return {[type]} [description]
       */
      onCopyError() {
        this.$message.error("复制链接失败,请手动复制")
      },
      operation(id) {
        console.log(id)
        // 弹框
        this.chooseUsersShow = true
        this.promRecordId = id
        this.file.ishowFile = true
      },
      /**
       * [phoneModel 手机号下载模板]
       * @return {[type]} [description]
       */
      phoneModel() {
        try {
          exportSheet.exportFile([{ phoneNo: '' }], '客户导入模板.xlsx', this.tempConfig)
          this.$notify.success({
            title: '温馨提示',
            message: '模板下载成功'
          })
        } catch (e) {
          this.$notify.error({
            title: '温馨提示',
            message: '下载遇到错误'
          })
        }
      },
       /**
       * [handleFileSelect 选择文件发生变化]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      handleFileSelect(e) {
        // 点击取消不做任何变化
        if(!e.target.files.length) {
          return
        }
        this.file = e.target.files[0] || {name: ''}
        let idx = this.file.name.lastIndexOf(".")
        let strLength = this.file.name.length
        let postf = this.file.name.substring(idx, strLength) // 后缀名

        if (!this.file.name || postf != '.xlsx') {
          this.$notify.warning({
            title: '提示',
            message: '请选择excel文件'
          })
          return
        }

        let items = []
        let parseConfig = {}
        for (let key in this.tempConfig) {
          parseConfig[this.tempConfig[key]] = key
        }
        items = exportSheet.parseToJSON(this.file, parseConfig)
        items.then((result) => {
          // 过滤空行
          let resultVal = result.filter(item => !!item.phoneNo)

          if(resultVal.length <= 0) {
            this.$notify.warning({
              title: '提示',
              message: '表格为空请重新上传'
            })
            return
          }
          this.phoneListData = resultVal
        })
      },
      /**
       * [sentUser 发券]
       * @return {[type]} [description]
       */
      sentUser(param) {
        if(param) {
          // 确认发圈
          if(this.phoneListData && this.phoneListData.length) {
            let phoneList = []
            this.phoneListData.forEach((item, i) => {
              phoneList.push(item.phoneNo)
            })
            this.chooseUsersShow = false
            this.file = {name: '', ishowFile: false}
            this.phoneListData = []
            this.$http.post(reqUrl.giveSpecifyProm, { promRecordId: this.promRecordId, phoneList: phoneList, remark: this.remark }).then(res => {
              if(res.code === 0) {
                this.$message.success("发券成功");
                this.remark = ''
                return
              }
              throw new Error(res.msg)
            }).catch(e => {
              this.remark = ''
              this.$notify.error({
                title: '提示',
                message: e.message
              })
            })
          }else{
            this.$message("请先导入客户")
          }
        }else{
          this.chooseUsersShow = false
          this.file = {name: '', ishowFile: false}
          this.phoneListData = []
          this.remark = ''
        }
      },
      changeStock(val) {
        this.detailParamMesg = {}
        this.disCoupon = {} // 满减券
        this.goodCoupon = {} // 商品券
        this.disIsnull = true // 满减券是否是空
        this.goodIsnull = true // 商品券是否是空
        this.promType = '2'
        this.currentPromDiscount = {} // 当前展示的卡券类型

        this.changeStockDialogShow = true
        this.changeStockDialogLoading = true
        this.$http.post(reqUrl.batchCreatePromInfo, {batchCreatePromId: val.id}).then(res => {
          this.changeStockDialogLoading = false
          if(res.code === 0) {
            if(res.data) {
              let promInfoVos = res.data.promInfoVos
              promInfoVos.forEach(item => {
                if(item.promType == 2){
                  this.disCoupon = item
                }else if(item.promType == 5){
                  this.goodCoupon = item
                }
              })
              if(this.disCoupon.promDiscounts && this.disCoupon.promDiscounts.length > 0) {
                this.disCoupon.promDiscounts.forEach(item => {
                  for(let key in item) {
                    if(item[key] != '') {
                      this.disIsnull = false
                    }
                  } 
                })
              }
              if(this.goodCoupon.promDiscounts && this.goodCoupon.promDiscounts.length > 0) {
                this.goodCoupon.promDiscounts.forEach(item => {
                  for(let key in item) {
                    if(item[key] != '') {
                      this.goodIsnull = false
                    }
                  } 
                })
              }
              this.detailParamMesg = res.data
              if(this.disIsnull) {
                this.currentPromDiscount = this.goodCoupon
                this.promType = '5'
              }else if(this.goodIsnull) {
                this.currentPromDiscount = this.disCoupon
                this.promType = '2'
              }else {
                this.currentPromDiscount = this.disCoupon
                this.promType = '2'
              }
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.changeStockDialogLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doChangeStockHandel() {
        // console.log("currentPromDiscount.promDiscounts:", this.currentPromDiscount.promDiscounts)
        let updatePromStock = this.currentPromDiscount.promDiscounts && this.currentPromDiscount.promDiscounts.length ? this.currentPromDiscount.promDiscounts.map(item => {
          return {
            curStock: item.curStock,
            promId: item.promId
          }
        }) : []
        for(let item of updatePromStock) {
          if(!item.curStock) {
            this.$message("库存不能为空，为大于零的正整数")
            return
          }else if(!countReg.test(item.curStock)) {
            this.$message("库存不能为空，为大于零的正整数")
            return
          }
        }
        this.isDisabled = true
        this.$http.post(reqUrl.updatePromStock, [...updatePromStock]).then(res => {
          this.isDisabled = false
          if(res.code === 0) {
            this.changeStockDialogShow = false
            this.$message.success("修改库存成功")
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }
  }
</script>


